<template>
  <v-container>
    <v-file-input
      label="Ti ln file Excel"
      @change="handleFileUpload"
      accept=".xlsx, .xls"
      outlined
    ></v-file-input>

    <v-btn @click="generateAndExport" :disabled="!fileReady">
      Ti file kt qu
    </v-btn>
  </v-container>
</template>

<script>
import * as XLSX from "xlsx";

export default {
  data() {
    return {
      templateSheet: [],
      danhsachSheet: [],
      fileReady: false,
    };
  },
  methods: {
    handleFileUpload(fileList) {
      const file = fileList instanceof FileList ? fileList[0] : fileList;

      const reader = new FileReader();
      reader.onload = (e) => {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: "array" });

        // c sheet Template
        const templateSheet = XLSX.utils.sheet_to_json(
          workbook.Sheets["Template"],
          { defval: "" }
        );

        this.templateSheet = templateSheet.map((row) => ({
          maTTHC: row["M TTHC"],
          tenTTHC: row["Tn TTHC"],
          cap: row["CAP"],
          donvi: row["DONVI"],
        }));
        console.log("templateSheet", this.templateSheet);

        // c sheet danhsach

        const danhsach = XLSX.utils.sheet_to_json(workbook.Sheets["danhsach"], {
          defval: "",
        });
        this.danhsachSheet = danhsach.map((row) => ({
          ma: row["M n v/phng, ban"],
          cap: row["Cp n v/phng, ban"],
        }));
        console.log("danhsachSheet", this.danhsachSheet);
        // Cho php bm nt
        this.fileReady = true;
      };

      reader.readAsArrayBuffer(file);
    },

    generateAndExport() {
      const output = [];

      this.templateSheet.forEach((row) => {
        const caps = (row.cap || "").split(";").map((c) => c.trim());

        let isFirst = true;

        // Nu c Cp Tnh

        // Vi Cp Huyn & X
        ["Cp Huyn", "Cp X"].forEach((capTarget) => {
          if (caps.includes(capTarget)) {
            this.danhsachSheet
              .filter((d) => d.cap === capTarget)
              .forEach((d) => {
                output.push({
                  "M TTHC": isFirst ? row.maTTHC : "",
                  "Tn TTHC": isFirst ? row.tenTTHC : "",
                  "n v": d.ma,
                  Cp: capTarget,
                });
                isFirst = false;
              });
          }
        });
      });

      // Xut file Excel
      const ws = XLSX.utils.json_to_sheet(output);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, "KetQua");
      XLSX.writeFile(wb, "ket_qua_tthc.xlsx");
    },

    // generateAndExport() {
    //   const output = [];

    //   this.templateSheet.forEach((row) => {
    //     const caps = (row.cap || "").split(";").map((c) => c.trim());

    //     if (caps.includes("Cp Tnh")) {
    //       output.push({
